.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 24px;
    background: white;
    border-bottom: 1px solid #eee;
  }

  .search-container {
    flex: 1;
    max-width: 360px;
    position: relative;
  }

  .search-box {
    width: 100%;
    padding: 10px 16px;
    padding-left: 40px;
    border: none;
    background: #f2fbf6;
    border-radius: 8px;
    font-size: 14px;
    outline: none;
    transition: background 0.2s;
  }

  .search-box:focus {
    background: #ffffff;
    box-shadow: 0 0 0 2px #d9f9e7;
  }

  .search-icon {
    position: absolute;
    left: 12px;
    top: 50%;
    transform: translateY(-50%);
    color: #00b14f;
  }

  .right-section {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  .icon-button {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background: none;
    cursor: pointer;
    color: #71717a;
    transition: background 0.2s;
  }

  .icon-button:hover {
    background: #f2fbf6;
  }

  .notification-button {
    position: relative;
  }

  .notification-dot {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 8px;
    height: 8px;
    background: #ef4444;
    border-radius: 50%;
    border: 2px solid white;
  }

  .avatar {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: #fbbf24;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }

  .avatar img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
  }

.icon-button-active{ // css for active button
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background: #f2fbf6;
  cursor: pointer;
  color: #00b14f;
}

.user-menu-popup{
  position: absolute;
  right: 0;
  margin-right: 0.2em;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 270px;
  padding: 10px;
  z-index: 100;
}

.user-menu-popup ul {
  list-style: none;
  padding: 0;
  
  margin: 0;
}

.user-menu-popup li {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  cursor: pointer;
  color: #4b5563;
  font-size: 16px;
}

.user-menu-popup li:hover {
  background-color: #f2fbf6;
}

.notification-popup{
  max-height: 500px;
  overflow-y: auto;
}

.notification-popup li{
  border-bottom: 1px solid #71717a;
}

.notification-seen {
  color: #00b14f;
  text-decoration: none;
  font-size: smaller;
  float: right;
  right: 0;
}